import Poloniex from 'poloniex-api-node';
import {Injectable} from '@angular/core';
import {Logger} from './logger.service';

@Injectable()
export class PoloniexService {
    private p = new Poloniex('E85XT0AU-BGRGZH3C-OCIUFAJ2-OX2PX0E2', '18baff52b0bea2abf59868da9ca17828e90bcda6b0c7b74249c13d3828ff648228d1e3010524ba2a2179be87db60aab497ba2917de1e788b8bcd4ca1466aea4d');

    constructor() {}

    private requestAPI(method: string, callback: Function, errCallback?: Function): void {
        this.p[method](function(err, res) {
            if (err) {
                if (typeof errCallback == 'function') {
                    errCallback(err);
                } else {
                    throw err;
                }
            } else {
                if (typeof callback === 'function') {
                    callback(res);
                } else {
                    Logger.error('No callback defined for API call <' + method + '>');
                }
            }
        }.bind(this));
    }

    public getBalances(callback: Function, errCallback?: Function): void {
        this.requestAPI('returnBalances', callback, errCallback);
    }
}


